.songs-preview {
  list-style: none;
  padding: 0;
  font-size: 14px;
}

.songs-preview li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  cursor: pointer;
  border-bottom: 1px solid #666;
  line-height: 40px;
}

.songs-preview li:hover {
  background: rgb(51, 51, 51);
}

.head {
  font-weight: 300;
  color: #b3b3b3;
  font-size: 12px;
}

.head-name {
  margin-left: 20px;
}

.songs-preview li span {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.song-name {
  width: 250px;
}
.song-album {
  width: 210px;
}
.song-artist {
  width: 180px;
}
.song-date {
  width: 100px;
}
.song-length {
  width: 60px;
}

.songs-preview .songlist .play-song {
  font-size: 20px;
  color: #b3b3b3;
}

.songs-preview > .active {
  color: #1db954;
  background: rgb(51, 51, 51);
}


